@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
.card {
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.daily {
    padding: 1rem;
    background-color: rgb(31, 31, 31);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
}

.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #f8dfff;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 10rem;
}

.chart-bar {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #c3b4f3;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.chart-bar__inner:hover {
    background-color: pink;
    border: 2px solid #313131;
    cursor: pointer;
}
.chart-bar__fill {
    background-color: #4826b9;
    width: 100%;
    transition: all 0.3s ease-out;
}

.chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
}

.daily-filter {
    color: white;
    padding: 0 1rem;
}

.daily-filter__control {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 1rem 0;
}

.daily-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.daily-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
}

.daily-list {
    list-style: none;
    padding: 0;
}

.daily-list__fallback {
    color: white;
    text-align: center;
}

.daily-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #4b4b4b;
}

.daily-item__description {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-flex-flow: column-reverse;
            flex-flow: column-reverse;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.daily-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0 1rem;
    color: white;
}

.daily-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
}

@media (min-width: 580px) {
    .daily-item__description {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        -webkit-flex: 1 1;
                flex: 1 1;
    }

    .daily-item__description h2 {
        font-size: 1.25rem;
    }

    .daily-item__price {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
}
.daily-date {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.new-daily__controls {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
}

.new-daily__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

.new-daily__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
}

.new-daily__control select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
}

.new-daily__actions {
    text-align: right;
}

.new-daily {
    background-color: #a892ee;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-daily button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
}

.new-daily button:hover,
.new-daily button:active {
    background-color: #510674;
    border-color: #510674;
}

.new-daily button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
}

.new-daily button.alternative:hover,
.new-daily button.alternative:active {
    background-color: #ddb3f8;
}

* {
    box-sizing: border-box;
}

html {
    font-family: 'Noto Sans JP', sans-serif;
}

body {
    margin: 0;
    background-color: #3f3f3f;
}

